//
// Pagination (multiple pages)
// --------------------------------------------------
.pagination {
    display: inline-flex;
    align-items: center;
    margin-top: 50px;
}

.pagination-prev { margin-right: 15px; }
.pagination-next { margin-left: 15px; }

.pagination-prev:hover { opacity: 0.8; transition: @transition-base; }
.pagination-next:hover { opacity: 0.8; transition: @transition-base; }

.pagination-disabled, .pagination-disabled:hover {
    opacity: 0.6; // @gray
    cursor: default;
}

.pagination-pages {
    margin: 0 15px 0 15px;
    color: @dark;
    font-size: @font-size-small; 
    font-weight: bold;
    line-height: 1;
}